html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#map {
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 0;
}

.splash {
  background-color: rgb(18, 136, 214);
  background-image: url("/images/splash.png");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1;
}

.menu {
  position: absolute;
  width: 50px;
  height: 50px;
  top: 0px;
  left: 0px;
  z-index: 1;
  color: black;
  font: bold 20px Arial;
}

#loginlink {
  color: white;
  font: bold 20px Arial;
}

#headerimg {
  width: 50px;
}

#heading {
  margin-left: 5px;
  font: bold 26px arial;
  color: white;
}

.custom-popup .leaflet-popup-content-wrapper {
  background: #2c3e50;
  color: #fff;
  font-size: 30px;
  line-height: 24px;
  border-radius: 0px;
}

.custom-popup .leaflet-popup-content-wrapper a {
  color: rgba(255, 255, 255, 0.1);
}

.custom-popup .leaflet-popup-tip-container {
  width: 30px;
  height: 15px;
}

.custom-popup .leaflet-popup-tip .leaflet-tooltip-tip {
  background: transparent;
  border: none;
  box-shadow: none;
}

.leaflet-tooltip-own {
  margin-left: 5px;
  background-color: transparent;
  border: none !important;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  color: white;
  font: bold 16px Arial;
  position: absolute;
  padding: 4px;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
}

.leaflet-container {
  background: rgb(34, 34, 34);
  outline: 0;
}
